.weather-card {
    background: linear-gradient(135deg, #4285f4 0%, #1a73e8 100%);
    color: white;
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
    max-width: 400px;
}

.weather-card.loading,
.weather-card.error {
    background: #f8f9fa;
    color: #5f6368;
    text-align: center;
    padding: 30px;
}

.weather-header {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 15px;
}

.weather-emoji {
    font-size: 48px;
}

.weather-location h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 500;
}

.weather-location span {
    font-size: 13px;
    opacity: 0.9;
}

.weather-temp {
    display: flex;
    align-items: baseline;
    gap: 15px;
    margin-bottom: 15px;
}

.temp-value {
    font-size: 48px;
    font-weight: 300;
}

.weather-desc {
    font-size: 16px;
    opacity: 0.9;
}

.weather-details {
    display: flex;
    gap: 20px;
    font-size: 13px;
    opacity: 0.9;
}

.weather-cache-note {
    margin-top: 12px;
    font-size: 11px;
    opacity: 0.8;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding-top: 10px;
}

.weather-loading {
    padding: 20px;
}